import { Component } from '@angular/core';
import { NavController, IonicPage } from 'ionic-angular';
import { Home } from '../../home/home';
import {Service} from '../../../providers/service/service';
import { Values } from '../../../providers/service/values';
import { Functions } from '../../../providers/service/functions';
//import { PrivacyPolicy } from '../../checkout/privacy-policy/privacy-policy';


/*
  Generated class for the AccountLoginPage page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/

@IonicPage()
@Component({
  templateUrl: 'register.html',
})
export class AccountRegister {

  info: any;
  zone: any;
  buttonText: any;
  registerData: any;
  loadRegister: any;
  public disableSubmit: boolean = false;
  

  constructor(public nav: NavController, public service: Service, public functions: Functions, public values: Values) {
    
    this.registerData = [];
    this.zone = [];
    this.info = {};
    this.registerData.newsletter=true;
    this.buttonText = this.values.lan.text_button_continue;  

    this.service.loadRegister()
    .then((results) => this.loadRegister = results);
  }

 
  register(a) {
    if(this.validateForm()){
      this.disableSubmit= true;
      this.buttonText=this.loadRegister.text_loading; 

      this.service.register(a)
      .then((results) => this.handleResults(results));
    }
  }
  
  
  handleResults(results){
    this.disableSubmit = false;
    this.buttonText= this.loadRegister.button_continue;
    this.loadRegister = results;

     
    if(results.error_warning){   
      this.functions.showAlert(this.values.lan.text_error, results.error_warning);
    }
    
    else if(results.logged_in == true){
      this.nav.setRoot(Home)
      .then((results) => this.handleRegister(results));
    }
  }  

 
  handleRegister(results){
    this.functions.showAlert(this.values.lan.text_success, this.values.lan.text_success_register);
  }
  
 
  validateForm(){

    if(this.registerData.agree == undefined || this.registerData.agree == ""){this.functions.showAlert(this.values.lan.text_error, this.values.lan.error_agree);return false}
    if(this.registerData.firstname == undefined || this.registerData.firstname == ""){return false}
    if(this.registerData.lastname == undefined || this.registerData.lastname == ""){return false}
    if(this.registerData.email == undefined || this.registerData.email == ""){return false}
    if(this.registerData.telephone == undefined || this.registerData.telephone == ""){return false}
    if(this.registerData.password == undefined || this.registerData.password == ""){return false}
    if(this.registerData.confirm == undefined || this.registerData.confirm == ""){return false}
    if(this.registerData.address_1 == undefined || this.registerData.address_1 == ""){return false}
    if(this.registerData.city == undefined || this.registerData.city == ""){return false}
    if(this.registerData.postcode == undefined || this.registerData.postcode == ""){return false}
    if(this.registerData.country_id == undefined){this.functions.showAlert(this.values.lan.text_error, this.values.lan.error_country); return false}

    if(this.registerData.zone_id == undefined || this.registerData.zone_id == null){
      this.registerData.zone_id = "0";
    }
    return true
  }

  
  getZone(country_id){
    this.registerData.zone_id = null;
  
    this.service.getZone(country_id)
    .then((results) => this.zone = results);
  }


  encodeUrl(href) {
    return href.replace(/&amp;/g, '&');
  }

 
  loadPolicy(){
    this.info.agree_link = this.encodeUrl(this.loadRegister.agree_link);
    this.info.title = this.loadRegister.agree_title;
    this.nav.push('PrivacyPolicy', this.info);
  }

}



